import React from "react";
import { QuestionProps } from "data/test5";

const VerifiedQuestion: React.FC<IQuestion> = ({
  question,
  currentQuestionId,
  userAnswer,
}) => {
  return (
    <div className="verified-question-container question-container">
      <p>{`${currentQuestionId}. ${question.questionText}`}</p>
      {question.answers.map((answer, id) => (
        <button
          name={`${question.id}`}
          value={id}
          key={id}
          className={`${userAnswer === id ? "selected" : ""} ${
            id === question.goodAnswerId ? "good" : ""
          }`}
        >
          {answer}
        </button>
      ))}
    </div>
  );
};

export interface IQuestion {
  question: QuestionProps;
  currentQuestionId: number;
  userAnswer: number | undefined;
}

export default VerifiedQuestion;
