import React from "react";
import { Container } from "reactstrap";
import { Link } from "react-router-dom";
import "swiper/swiper.scss";
import QuizSwiper from "components/elements/QuizSwiper";
import { QuestionProps } from "data/test5";
import Timer from "react-compound-timer";

const QuizPage: React.FC<IQuizPage> = ({ title, questions }) => {
  return (
    <section>
      <Container>
        <Link to="/">Przejdź do ekranu początkowego</Link>
        <h2>
          {title} -{" "}
          <Timer formatValue={(value) => `${value < 10 ? `0${value}` : value}`}>
            <Timer.Minutes />:<Timer.Seconds />
          </Timer>
        </h2>
        <QuizSwiper questions={questions} />
      </Container>
    </section>
  );
};

export interface IQuizPage {
  title: string;
  questions: QuestionProps[];
}

export default QuizPage;
