import React, { useState, useEffect, useRef } from "react";
import Swiper, { SwiperRefNode } from "react-id-swiper";
import SwiperInstance from "swiper";
import { Button, Modal, ModalBody } from "reactstrap";
import Question from "components/elements/Question";
import "swiper/swiper.scss";
import { UserAnwsersProps } from "hooks/useQuizData";
import { QuestionProps } from "data/test5";
import VerifiedQuestion from "../VerifiedQuestion";

const QuizSwiper: React.FC<IQuizSwiper> = ({ questions }) => {
  const [error, toggleError] = useState(false);
  const [result, setResult] = useState<ResultProps[]>();
  const [userAnswers, setUserAnswers] = useState<UserAnwsersProps>({});
  const [goodAnswers, setGoodAnswers] = useState(0);
  const [swiper, setSwiper] = useState<SwiperInstance | null>(null);
  const [lastSlide, toggleLastSlide] = useState<boolean>(false);
  const [modal, setModal] = useState(false);
  const ref = useRef<SwiperRefNode>(null);

  const toggle = () => {
    setModal(!modal);
    window.location.href = "/";
  };

  const goNext = () => {
    if (!swiper) {
      return;
    }
    if (lastSlide) {
      checkResult();
      return;
    }
    swiper.slideNext();
  };

  const goPrev = () => {
    if (!swiper) {
      return;
    }
    swiper.slidePrev();
  };

  const handleAnswerClick = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    if (!swiper) {
      return;
    }
    const target = e.target as HTMLButtonElement;
    const questionId = target.name;
    const answerNumber = parseInt(target.value);

    setUserAnswers((prev) => ({ ...prev, [questionId]: answerNumber }));
  };

  const checkResult = () => {
    if (Object.keys(userAnswers).length !== questions.length) {
      toggleError(true);
      return;
    }
    toggleError(false);
    setModal(true);
    let result = Object.keys(userAnswers).map((key) => ({
      questionId: parseInt(key),
      answerId: userAnswers[key],
      goodAnswer: questions.find((item) => item.id === parseInt(key))
        ?.goodAnswerId,
    }));
    let good = 0;
    result.forEach((answer) => {
      if (answer.goodAnswer === answer.answerId) good += 1;
    });
    setResult(result);
    setGoodAnswers(good);
  };

  useEffect(() => {
    if (!ref.current || !ref.current.swiper) {
      return;
    }
    setSwiper(ref.current.swiper);
  }, []);

  return (
    <>
      <Swiper
        freeMode={false}
        ref={ref}
        allowTouchMove={false}
        slidesPerView="auto"
        on={{
          slideChange: () => {
            if (!ref.current) {
              return;
            }
            let swiper = ref.current.swiper;
            let isEnd = !!swiper?.isEnd;
            toggleLastSlide(isEnd);
          },
        }}
      >
        {questions.map((question, id) => (
          <div className="slide" key={id}>
            <Question
              question={question}
              currentQuestionId={id + 1}
              handleAnswerClick={handleAnswerClick}
            />
          </div>
        ))}
      </Swiper>
      <div className="nav">
        {error && (
          <p className="text-danger text-center">
            Nie zaznaczono odpowiedzi na wszystkie pytania
          </p>
        )}
        <div className="nav-wrapper">
          <Button color="secondary" onClick={goPrev}>
            Poprzednie
          </Button>
        </div>
        <div className="nav-wrapper">
          <Button color="primary" onClick={goNext}>
            {lastSlide ? "Sprawdź wynik" : "Następne"}
          </Button>
        </div>
      </div>
      <Modal isOpen={modal} toggle={toggle}>
        <ModalBody>
          <p>Wynik:</p>
          <p
            className={goodAnswers >= 11 ? "text-success" : "text-danger"}
          >{`${goodAnswers} / ${Object.keys(userAnswers).length}`}</p>
          <hr />
          <div className="quiz-result mt-3">
            {questions.map((item, id) => (
              <div key={id}>
                <VerifiedQuestion
                  question={item}
                  currentQuestionId={id + 1}
                  userAnswer={
                    result &&
                    result.find((i) => i.questionId === item.id)?.answerId
                  }
                />
              </div>
            ))}
          </div>
        </ModalBody>
        <Button color="secondary" onClick={toggle}>
          Powrót do ekranu głównego
        </Button>
      </Modal>
    </>
  );
};

type ResultProps = {
  questionId: number;
  answerId: number;
  goodAnswer: number | undefined;
};

export interface IQuizSwiper {
  questions: QuestionProps[];
}

export default QuizSwiper;
