import { questions as test1questions } from "data/test1";
import { questions as test5questions } from "data/test5";
import { questions as test4questions } from "data/test4";
import shuffle from "helpers/shuffle";

// const useQuizData = () => shuffle(test5questions).slice(0, 15);
const useQuizData = (id: number) => {
  const test1 = shuffle(test1questions).slice(0, 15);
  const test4 = shuffle(test4questions).slice(0, 15);
  const test5 = shuffle(test5questions).slice(0, 15);

  switch (id) {
    case 0:
      return test1;
    case 1:
      return test5;
    case 2:
      return shuffle(test1.concat(test5)).slice(0, 15);
    case 3:
      return test4;
    default:
      return test1;
  }
};
export type UserAnwsersProps = {
  [key: string]: number;
};

export default useQuizData;
