import React from "react";
import { Switch, Route } from "react-router-dom";
import HomePage from "components/views/HomePage";
import useQuizData from "hooks/useQuizData";
import QuizPage from "./QuizPage";

const Views: React.FC = () => {
  return (
    <Switch>
      <Route path="/" exact>
        <HomePage />
      </Route>
      <Route path="/test1">
        <QuizPage title="Test 1" questions={useQuizData(0)} />
      </Route>
      <Route path="/test4">
        <QuizPage title="Test 4" questions={useQuizData(3)} />
      </Route>
      <Route path="/test5">
        <QuizPage title="Test 5" questions={useQuizData(1)} />
      </Route>
      <Route path="/mix">
        <QuizPage title="Test 1 & 5" questions={useQuizData(2)} />
      </Route>
    </Switch>
  );
};

export default Views;
