import React, { useState } from "react";
import { QuestionProps } from "data/test5";

const Question: React.FC<IQuestion> = ({
  question,
  currentQuestionId,
  handleAnswerClick,
}) => {
  const [activeAnswer, setActiveAnswer] = useState<number | null>(null);

  const handleClick = (e: React.MouseEvent<HTMLButtonElement>, id: number) => {
    setActiveAnswer(id);
    handleAnswerClick(e);
  };
  return (
    <div className="question-container">
      <h4>{`${currentQuestionId}. ${question.questionText}`}</h4>
      {question.answers.map((answer, id) => (
        <button
          name={`${question.id}`}
          value={id}
          onClick={(e) => handleClick(e, id)}
          key={id}
          className={activeAnswer === id ? "active" : ""}
        >
          {answer}
        </button>
      ))}
    </div>
  );
};

export interface IQuestion {
  question: QuestionProps;
  currentQuestionId: number;
  handleAnswerClick(e: React.MouseEvent<HTMLButtonElement, MouseEvent>): void;
}

export default Question;
